<template>
  <v-container grid-list-md fluid>
    <v-card>
      <v-card-text>
        <v-layout row wrap>
          <v-flex xs12 sm12 md8 lg8>
            <v-text-field v-model="timeToSave" label="Czas, po kt√≥rym dane, bƒôdƒÖ szukane po API"></v-text-field>
          </v-flex>
          <v-flex ml-5 xs12 sm12 md3 lg3>
            <v-radio-group v-model="typeFromTemplate" :mandatory="false">
            <v-radio
              :label="timeType[0]"
              :value="timeType[0]"
            ></v-radio>
            <v-radio
              :label="timeType[1]"
              :value="timeType[1]"
            ></v-radio>
            <v-radio
              :label="timeType[2]"
              :value="timeType[2]"
            ></v-radio>
            </v-radio-group>
            <!-- <v-select :items="timeType" label="Jednostka czasu" v-model="type"></v-select> -->
          </v-flex>
        </v-layout>
        <v-btn
          color="blue darken-3"
          :dark="timeToSave!=null & timeToSave!='' & typeFromTemplate!=null ? true:false"
          :disabled="timeToSave!=null & timeToSave!='' & typeFromTemplate!=null ? false:true"
          @click="saveTime()"
        >
          <v-icon>save</v-icon>Zapisz
        </v-btn>
      </v-card-text>
    </v-card>
  </v-container>
</template>


 
<script>
import axios from "axios";

export default {
  data() {
    return {
      configId: null,
      items: null,
      timeToSave: null,
      typeFromTemplate: null,
      timeType: ['Sekundy', 'Minuty', 'Godziny']
    };
  },
  methods: {
    saveTime() {
      let timeToSend = null
      let myPromise = new Promise(resolve=>{
        if (this.typeFromTemplate === "Minuty") {
          timeToSend = parseInt(this.timeToSave) * 60;
        }else if (this.typeFromTemplate === "Godziny") {
          timeToSend = parseInt(this.timeToSave) * 360;
        }else{
          timeToSend = parseInt(this.timeToSave)
        }
        resolve(timeToSend)
      }).then(result=>{
        let dataToSend = {
          config_id: this.configId,
          time_config: result
        };
        axios
          .post("/api/search/set_configure_data/", dataToSend)
          .then(response => {
            console.log(response.data);
          })
          .catch(error => {
            console.log(error.response);
          });

      })
    },
    downloadData() {
      axios.get("/api/search/get_configuration_data/").then(response=>{
        if(response.data){
          this.configId = response.data[0].id
          this.timeToSave = response.data[0].time_config
          this.typeFromTemplate = "Sekundy"
        }
      })
    }
  },
  mounted(){
    this.downloadData()
  }
};
</script>

<style scoped>
</style>



