<template>
  <v-container>
    <v-card>
      <v-card-text>
        <v-layout row wrap>
          <v-flex xs12 sm12 md9 lg9>
            <v-text-field outline v-model="toSearch" label="Szukana fraza"></v-text-field>
          </v-flex>
          <v-flex ml-5 xs12 sm12 md2 lg2>
            <v-btn
              color="blue darken-3"
              :dark="toSearch===null || toSearch==='' ? false:true"
              :disabled="toSearch===null || toSearch==='' ? true:false"
              @click="testBackend()"
            >
              <v-icon>search</v-icon>Szukaj
            </v-btn>
          </v-flex>
        </v-layout>
        <v-subheader v-if="searchInformation">Czas odpowiedzi: {{searchInformation.answerTime}}s</v-subheader>
        <v-subheader v-if="searchInformation">Liczba wszystkich wynik√≥w: {{searchInformation.allResultsItemsCount}}</v-subheader>
        <v-data-table :headers="headers" :items="items">
          <template v-slot:items="props">
            <td class="text-xs-left">{{props.item.position}}</td>
            <td class="text-xs-left">{{props.item.displayLink}}</td>
            <td class="text-xs-left">{{props.item.title}}</td>
            <td class="text-xs-left"><a :href="props.item.link">{{props.item.link}}</a></td>
            <td class="text-xs-right">{{props.item.count_words}}</td>
          </template>
        </v-data-table>
      </v-card-text>
    </v-card>
  </v-container>
</template>



<script>
import axios from "axios";

export default {
  data() {
    return {
      items: [],
      searchInformation: null,
      toSearch: null,
      howManyItems: null,
    };
  },
  methods: {
    testBackend() {
      axios
        .post("/api/search/get_data/", {search_item: this.toSearch})
        .then(response => {
          this.items = response.data.items
          this.searchInformation = {
            answerTime: response.data.searchInformation.formattedSearchTime,
            allResultsItemsCount: response.data.searchInformation.formattedTotalResults,
          }
          console.log(response.data);
        })
        .catch(error => {
          console.log(error.response);
        });
    }
  },
  computed:{
    headers () {
      return [
          {
            text: "Pozycja",
            align: "left",
            sortable: true,
            value: "position"
          },
          {
            text: "Link",
            align: "left",
            sortable: true,
            value: "displayLink"
          },
          {
            text: "Tytu≈Ç",
            align: "left",
            sortable: true,
            value: "title"
          },
          {
            text: "Link do wyniku wyszukania",
            align: "left",
            sortable: true,
            value: "link"
          },
          {
            text: "Ilo≈õƒá s≈Ç√≥w w tytule",
            align: "right",
          },
        ]
      

    }
  }
};
</script>

<style scoped>
</style>



