<template>
  <div id="app">
    <v-app>
      <v-toolbar fixed app clipped-right dark color="blue darken-3">
        <v-toolbar-side-icon @click="mini=!mini"></v-toolbar-side-icon>
        <v-toolbar-title class="white--text">Pawe≈Ç Jarkowski - Aplikacja na potrzeby rekrutacji.</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-tooltip bottom>
          <template v-slot:activator="{ on }">
            <v-btn icon v-on="on">
              <a
                class="link-a"
                href="https://pl.linkedin.com/in/pawe%C5%82-jarkowski-226675134"
                target="_blank"
              >
                <v-icon>account_circle</v-icon>
              </a>
            </v-btn>
          </template>
          <span>Linkedin</span>
        </v-tooltip>
        <v-menu v-model="openDial" :close-on-content-click="false" :nudge-width="100" offset-y>
          <template v-slot:activator="{ on }">
            <v-btn v-model="openDial" color="blue darken-2" dark fab v-on="on">
              <v-icon>contact_mail</v-icon>
              <v-icon>close</v-icon>
              </v-btn>
          </template>
          <v-card>
            <v-card-text>
              <p style="color: black;">{{mail}}</p>
            </v-card-text>
          </v-card>
        </v-menu>
      </v-toolbar>
      <v-navigation-drawer
        clipped
        app
        fixed
        v-model="drawer"
        :mini-variant.sync="mini"
        hide-overlay
        stateless
      >
        <v-toolbar flat class="transparent">
          <v-list class="pa-0">
            <v-list-tile avatar>
              <v-list-tile-avatar></v-list-tile-avatar>

              <v-list-tile-content>
                <v-list-tile-title>Menu</v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
          </v-list>
        </v-toolbar>

        <v-list class="pt-0" dense>
          <v-divider></v-divider>

          <v-list-tile v-for="item in items" :key="item.title" @click="pushToComponent(item)">
            <v-list-tile-action>
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-tile-action>

            <v-list-tile-content>
              <v-list-tile-title>{{ item.title }}</v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
        </v-list>
      </v-navigation-drawer>
      <v-content>
        <v-container fluid>
          <router-view></router-view>
        </v-container>
      </v-content>
    </v-app>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      drawer: true,
      mail: "pjarkowski@edu.cdv.pl",
      items: [
        {
          title: "Szukaj w google",
          icon: "search",
          push: "/"
        },
        {
          title: "Konfiguracja",
          icon: "settings",
          push: "SetConfigure"
        }
      ],
      openDial: false,
      mini: true,
      right: null
    };
  },
  methods: {
    pushToComponent(item) {
      this.$router.push(item.push);
    }
  }
};
</script>

<style>
#app {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  margin: 0px;
}
.reset-margin {
  margin: 0px;
}
.full-height {
  height: 100vh !important;
}
.link-a:link {
  text-decoration: none;
  color: white;
}
</style>



