import Vue from 'vue'
import Router from 'vue-router'
import SearchGoogle from '@/components/SearchGoogle'
import SetConfigure from '@/components/SetConfigure'

Vue.use(Router)


export default new Router({
  mode: 'history',
  hash: false,
  routes: [
    {
      path: '/',
      name: 'SearchGoogle',
      component: SearchGoogle
    },
    {
      path: '/SetConfigure',
      name: 'SetConfigure',
      component: SetConfigure
    }
  ]
})



